.pdp__user-guide-content {
    margin: 2rem 0 1rem;
}

.pdp__product-spec-wrapper {
    margin-bottom: 3.5rem;
}

    .pdp__product-spec {
        margin-top: 1.5rem;
    }

    .pdp__key-feature-title {
        margin: 1.25rem 0;
    }

@include media-query(small-up) {
    .pdp__details {
        margin: 3rem 0;
    }

        .pdp__details-item {
            margin: 3rem 0;

            &:not(.toggle--active) {
                display: none;
            }
        }

        .pdp__details-tabs {
            border-bottom: solid .0625rem $color-grey-4;
        }

        .pdp__details-tab-item {
            padding-bottom: .5rem;

            &.toggle--active {
                font-weight: $font-weight-semibold;
                pointer-events: none;
            }

            &:not(:last-of-type) {
                margin-right: 1.75rem;
            }
        }
}

@include media-query(small) {
    .pdp__details {
        margin-bottom: 2rem;
    }

        .pdp__details-item {
            border-bottom: solid .0625rem $color-grey-3;
        }

        .pdp__details-title {
            padding: 1.5rem 0;
            cursor: pointer;

            &.toggle--active {
                .pdp__details-title-icon {
                    transform: rotate(180deg);
                }
            }
        }

        .pdp__details-title-icon {
            transition: $transition-speed-medium;
        }

        .pdp__details-description {
            margin: 1rem 0 2rem;

            &:not(.toggle--active) {
                display: none;
            }
        }

        .pdp__key-feature-content {
            margin-bottom: 2.5rem;
        }
}
.contactus-tile__icon {
    width: 1.375rem;
}
