

.personalisation__gift-option-content {
    &:not(.toggle--active) {
        display: none;
    }
}

.personalisation__card {
    box-shadow: 0 0 .25rem .0625rem transparentize($color-black, .9);
}

    .personalisation__card-logo {
        display: inline-block;
        width: 8.25rem;
        height: 2.4rem;
        margin-bottom: 2.5rem;
    }

    .personalisation__card-textarea {
        border-color: $color-grey-3;
        text-align: inherit;
    }

    .personalisation__img--engraving {
        max-height: 25rem;
        max-width: 20rem;
    }

@include media-query(small-up) {
    .personalisation__card {
        padding: 1.5rem 4.375rem 3.125rem;
    }

    .personalisation__padded-container {
        padding-right: 1.5rem;
    }

    .personalisation__cta-save {
        max-width: 23.75rem;
    }

    .personalisation__cta-cancel {
        max-width: 10.875rem;
    }
}

@include media-query(small) {
    .personalisation__card {
        padding: 1.5rem 1.75rem 3.25rem;
    }

    .personalisation__img-col {
        margin-bottom: 1.5rem;
    }
}
