
.pdp {
    position: relative;
}

    .pdp-main {
        margin-bottom: 1.875rem;
        overflow: hidden;
    }

    .pdp-main__media {
        position: relative;
        @media screen and (min-width:768px) {
            padding-left: 3.313em;
        }
    }

    .pdp-main__breadcrumbs {
        position: relative;
        z-index: 1;
        //This fixes an odd iOS Safari issue where the search bar would render under the PDP's gallery:
        //https://share.getcloudapp.com/p9ubxJ2p
        transform: translate3d(0,0,0);
        margin: 1rem 0;
        @media screen and (max-width: 1440px) {
            padding: 0 1rem;
        }
    }

    .pdp__details-short-description-wrapper {
        margin-top: .875rem;
    }

    .pdp-main__price {
        margin-top: 1rem;
        margin-bottom: .875rem;
    }

        .pdp-main__price-value {
            margin-right: 1.125rem;
        }

    .pdp-main__description {
        &.toggle--active {
            .pdp-main__description-truncated {
                display: none;
            }
        }

        &:not(.toggle--active) {
            .pdp-main__description-full {
                display: none;
            }
        }
    }

    .pdp-main__section--actions {
        position: relative;
        margin-top: 1rem;
        margin-bottom: 2.5rem;
    }

    .pdp-main__section--secondary-actions {
        margin-bottom: 2rem;
    }

        .pdp-main__secondary-action {
            &:not(:last-child) {
                margin-bottom: 1.125rem;
            }

            .white-line {
                font-size: 1.5rem;
            }
        }

        .pdp-main__footer-item {
            &:not(:last-child) {
                margin-right: 2.5rem;

                &:after {
                    display: inline-block;
                    margin-left: 2.5rem;
                    width: .0635rem;
                    height: 1.75rem;
                    background-color: $color-grey-3;
                    content: '';
                }
            }
        }

    .pdp-straps__description {
        margin: 1rem 0;
    }

    .pdp-straps__item {
        margin: 0 1.5rem 1rem;
        &.toggle--active {
            .pdp-straps__item-image-wrapper,
            .pdp-straps__item-image {
                border-color: $color-red;
            }
        }
    }

        .pdp-straps__item-image-wrapper {
            padding-bottom: 2px;
            margin-bottom: .5rem;
            border-bottom: solid 5px $color-white;
            transition: $transition-speed-medium;
            max-width: 5.75rem;
        }

        .pdp-straps__item-image {
            border: solid 1px $color-grey-3;
            transition: $transition-speed-medium;
            width: 84px;
            height: 84px;
        }

        .pdp-straps__type-image {
            width: 90px;
            height: 92px;
            &.first-image {
                transform: rotate(90deg);
            }
        }

    .pdp-straps__tab-list {
        & > :nth-child(3) {
            margin-top: .5rem;
            padding: .5rem 0;
            min-width: 100%;
        }

        & > :only-child {
            max-width: 100%;
            min-height: auto;
        }
    }

        .pdp-straps__tab-item {
            padding: .25rem;
            max-width: 48%;
            width: 48%;
            min-height: 2.5rem;
            line-height: 1.25;
            &.toggle--active {
                background-color: $color-grey-7;
                border-color: $color-grey-7;
                color: $color-white;
            }
        }

    .pdp-straps__carousel {
        max-width: 20rem;
        margin: 0 auto 1.5rem;

        .slick-disabled {
            opacity: .4;
            visibility: visible;
            pointer-events: none;
        }

        .slick-prev {
            left: -2rem;
        }

        .slick-next {
            right: -2rem;
        }

        .icon {
            font-size: $font-size-hecto;
            color: $color-grey-5;
        }

        .pdp-straps__item {
            margin: 0;
        }
    }

@include media-query(small-up-to-medium-down) {
    .pdp-straps__tab-item {
        font-size: $font-size-micro;
    }
}

@include media-query(xxsmall-up-to-medium) {
    .pdp-straps__tab-list {
        .pdp-straps__tab-item.toggle--active:hover {
            opacity: 1;
        }
    }
}

@include media-query(small-up) {
    .pdp-main__details {
        padding-top: 2.5rem;
        padding-bottom: 2rem;

        &:not(.fixit--is-tall) {
            transition: top $transition-speed-fast $transition-ease-out;
        }
    }

    .pdp-main__section {
        max-width: 25rem;
    }

        .pdp-main__section--name {
            margin-bottom: 2rem;
        }

        .pdp-main__section--attributes {
            margin-bottom: .5rem;
        }

        .pdp-main__number-rating {
            margin-top: 1.625rem;
        }

        .pdp-main__promotions {
            margin-bottom: .875rem;
        }

        .pdp-straps__item {
            margin-bottom: 1.5rem;
        }

        .pdp-straps__description {
            margin: 2rem 0 1.75rem;
        }

        .pdp-straps__carousel {
            .slick-prev {
                left: -2.75rem;
            }

            .slick-next {
                right: -2.75rem;
            }
        }
}

@include media-query(small) {

        .pdp-main__section {
            margin-top: 1.25rem;
        }

        .pdp-main__section--actions {
            padding-top: 1.25rem;
            padding-bottom: 1.75rem;
        }

        .pdp-main__number-rating {
            margin-top: 1rem;
        }

        .pdp-main__availability {
            margin-top: 3rem;
        }
}