
.modal--product-zoom {
    .window-modal__content {
        width: 100%;
        height: 100vh;
        overflow: hidden;
    }
}

.product-zoom {
    height: 100vh;
    overflow: auto;

    .slick-list {
        width: 100%;
        margin: auto;
        max-width: breakpoint(large);
    }
}

    .product-zoom__label {
        z-index: 1;
        position: absolute;
        top: 0;
        left: 0;
        padding: 1.625rem;
    }

    .product-zoom__arrow {
        position: fixed;
        top: calc(50% - 1.1875rem);
    }

    .product-zoom__image {
        width: 80%;
        height: 80%;
    }

@include media-query(small-up) {
    .product-zoom__arrow {

    }
}

@include media-query(small) {
    .modal--product-zoom {
        .window-modal__content {

        }
    }
}
